import { TextInputComponent } from '../text-input-component';
import {
  ButtonsContainer,
  AuthorizeButton,
  Container,
  InputWrapper,
  RequestAccessButton,
  Title,
} from './emoition-components';
import { TAuthPage } from './types';

export function AuthWindow({
  login,
  password,
  setLogin,
  setPassword,
  authClickHandler,
  errorMessage,
}: TAuthPage) {
  return (
    <Container component="section">
      <form onSubmit={authClickHandler}>
        <Title variant="h2"></Title>
        <InputWrapper>
          <TextInputComponent
            title=""
            setValue={setLogin}
            value={login}
            placeholder=" "
            type="Text"
          />
        </InputWrapper>
        <InputWrapper>
          <TextInputComponent
            title=""
            setValue={setPassword}
            placeholder=" "
            value={password}
            type="password"
            errorMessage={errorMessage}
          />
        </InputWrapper>
        <ButtonsContainer>
          <RequestAccessButton> </RequestAccessButton>
          <AuthorizeButton variant="contained" type="submit">
            
          </AuthorizeButton>
        </ButtonsContainer>
      </form>
    </Container>
  );
}
