import { TFilterDataArr } from './types';

export const filterDataArr = ({ data, activeObjArr }: TFilterDataArr) => {
  return [
    {
      title: '',
      data: data[0],
      activeTitle: activeObjArr[0],
    },
    {
      title: '',
      data: data[1],
      activeTitle: activeObjArr[1],
    },
    {
      title: '',
      data: data[2],
      activeTitle: activeObjArr[2],
    },
    {
      title: '',
      data: data[3],
      activeTitle: activeObjArr[3],
    },
  ];
};
